import axios from 'axios';

	


function request(params) {
	const host = 'http://service-62glooo6-1256773370.gz.apigw.tencentcs.com/release/';
	if(params.type == 'get'){
		return new Promise((resolve, reject) => {
			axios.get(host+params.url,{params:params.params})

				.then((response) => {
					resolve(response.data);
				})
				.catch((error) => {
					reject(error);
				});
		})
	}
	
	if(params.type == 'post'){
		return new Promise((resolve, reject) => {
			axios.post(host+params.url, params.params)
				.then((response) => {
					resolve(response.data);
				})
				.catch((error) => {
					reject(error);
				});
		})
	}
}

export default {
    request
}